import React, { useCallback } from 'react';
import WidgetCard from 'components/Cards/WidgetCard';
import { useStaticQuery, graphql, Link } from 'gatsby';
import { Chip, List, ListItem, ListItemText, ListItemSecondaryAction, makeStyles, Theme } from '@material-ui/core';
import { linkEvent } from 'utils/loggers';
import { Group } from 'models/post';
import kebabCase from 'lodash/kebabCase';

const useStyles = makeStyles<Theme>(theme => ({
  link: {
    color: 'inherit',
    textDecoration: 'none',
  },
  item: {
    listStyle: 'none',
    padding: theme.spacing(1),
    borderRadius: theme.shape.borderRadius / 2,
    overflow: 'hidden',
  },
  chip: {
    borderRadius: theme.shape.borderRadius,
    fontSize: '0.6rem',
    height: 'auto',
    padding: '0.3rem 0',
    backgroundColor: theme.palette.grey['200'],
  },
}));

const CategoriesCard = () => {
  const styles = useStyles();
  const {
    allMarkdownRemark: { group: categories },
  }: Group = useStaticQuery(graphql`
    query CategoriesQuery {
      allMarkdownRemark {
        group(field: frontmatter___categories) {
          fieldValue
          totalCount
        }
      }
    }
  `);

  const handleCategoryClick = useCallback((url: string) => {
    linkEvent(url, '카테고리 링크 클릭');
  }, []);

  return (
    <WidgetCard title="카테고리">
      <List component="nav" dense={true}>
        {categories.map(({ fieldValue, totalCount }, index) => (
          <Link key={index} to={`/categories/${kebabCase(fieldValue)}`} className={styles.link}>
            <ListItem
              button
              className={styles.item}
              onClick={() => handleCategoryClick(`/categories/${kebabCase(fieldValue)}`)}
            >
              <ListItemText>{fieldValue}</ListItemText>
              <ListItemSecondaryAction>
                <Chip className={styles.chip} label={totalCount} size="small" />
              </ListItemSecondaryAction>
            </ListItem>
          </Link>
        ))}
      </List>
    </WidgetCard>
  );
};

export default CategoriesCard;
